  /* color:#007bed; MAIN BLUE*/
  /* #7c7c7d secondary font color */
  /* color: #434344; SECONDARY GREY*/

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

body {
  background-color: #212122;
}


/* NAVBAR */
#nav {
  width: 100%;
  height: 55px;
  margin: 0;
  padding: 0;
  position: fixed;
  background-color: #434344 !important;
  z-index: 1000;
}

#nav #logo {
  height: 100%;
  margin-top: 4px;
  margin-left: 8px;
  padding: 8px;
  display: inline-block;
  color:  #f4f4f5;
  cursor: pointer;
  font-size: 1.5em;
}

.links {
  height: 0;
  overflow: hidden;
  list-style-type: none;
  font-weight: bold;
  text-align: center;
  /* float: right; */
}

#nav a {
  margin: 4px;
  display: block;
  color: #f4f4f5;
  cursor: pointer;
}

button {
  font-size: 32px;
  margin-right: 8px;
  margin-top: 12px;
  color:  #f4f4f5;
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  float: right;
}

.show-nav {
  margin-top: -10px;
  height: 120px;
  width: 100%;
  background-color: #434344;
  transition: 1s;
}

.hide-nav {
  height: 0;
  transition: 1s;
  animation-direction: alternate;
}

.link-wrapper > .active {
  transition: color 0.4s; /* For modern browsers */
  color:#7c7c7d !important; 
}

@media screen and (min-width: 750px) {
  button {
      display: none;
  }

  .links {
      height: 100%;
      float: right;
  }

  .link-wrapper {
    height: 100%;
    padding: 8px;
    margin-top: 8px;
    display: inline-block;
  }

}

/* SECTION CSS */
.about-section {
  padding-top: 60px;
  height: 70vh;
  color: #f4f4f5;
  text-align: center;
  overflow:hidden;
}

.experience-section {
  margin-bottom: 0px; /* Account for short tutor description */
}

.education-section {
  margin-bottom: 50px; /* Account for long course work */
}

.skills-section {
  margin-bottom: 25px;
}

.projects-section {

}

.section-heading {
  color: #f4f4f5;
  font-size: 2.2em;
  text-align: center;
}

.divider {
  margin-bottom: 1em;
  background-color: #7c7c7d;
  border: none;
  display: block;
  height: 2px;
  overflow: visible;
  position: relative;
  width: 100%;
}

.divider:before {
  background-color: #007bed;
  content: '';
  display: block;
  height: 2px;
  left: 0;
  position: absolute;
  width: 15%;
  z-index: 1;
}

#section-wrapper {
  display: block;
  width: 300px;
  height: 100%;
  margin: auto;
}

#projects-section-wrapper {
  display: block;
  width: 90%;
  height: 100%;
  margin: auto;
  margin-bottom: 16px;
  text-align: center;
}

@media screen and (min-width: 750px) {
  #section-wrapper {
    width: 500px;
  }
  
  #projects-section-wrapper {
    width: 600px;
    width: 75%;
  }

  .section-heading {
    text-align: left;
  }
}

@media screen and (min-width: 1300px) {
  #section-wrapper {
    width: 600px;
  }

  #projects-section-wrapper {
    width: 900px;
  }
}


/* ABOUT PAGE */
.nametag {
  height: 100%;
  width: 85%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.subheading {
  color: #7c7c7d;
  font-size: 1.5em;
}

#name-nametag {
  font-size: 2.5em;
  margin-bottom: .3em;
}

.content {
  color: #7c7c7d;
  margin-bottom: .5em;
}

#headshot {
  border-radius: 50%;
  border: 5px solid #434344;
  width: 150px;
}

#photo {
  display: none;
}

#bio {
  flex-basis: 100%;
  flex: 1;
  text-align: left;
}

.website-link {
  color: #f4f4f5;
  font-size: x-large;
  margin-right: 8px;
  transition: 0.2s;
}

.website-link:hover {
  color: #f4f4f5;
  opacity: .5;  
  transition: 0.2s;
}

@media screen and (min-width: 850px) {
  .nametag {
    width: 800px;
    flex-wrap: nowrap;
  }

  #headshot {
    border-radius: 50%;
    border: 5px solid #434344;
    width: 300px;
    height: auto;
    margin-right: 32px;
  }
  
  #photo {
    display: flex;
    max-width: 100%;
    width: 300px;
    height: 300px;
    margin-bottom: 0;
    margin-right: 20px;
  }
  
  #bio {
    flex-basis: 85%;
    width: 80px;
    flex: auto;
    text-align: left;
  }
}

/* List Component */

.list-item {
  display: block;
  width: 100%;
  height: 110px;
}

.title-label {
  float: left;
  color:  #f4f4f5;
  font-weight: bold;
  /* margin-bottom: .5em; */
}

.year-label {
  float: left;
  color:  #f4f4f5;
}

.position-label {
  float: left;
  font-style: italic;
  color:  #f4f4f5;
}

.gpa-label {
  float: left;
  color:  #f4f4f5;
}

.description-label {
  margin-top: 4px;
  float: left;
  color:  #f4f4f5;
}

#icon {
  display: none;
}

.new-newline {
  display: none;
}

@media screen and (min-width: 750px) {
  .title-label {
    float: left;
    font-size: 1em;
  }
  
  .year-label {
    float: right;
  }
  
  .position-label {
    float: left;
  }
  
  .gpa-label {
    float: right;
  }
  
  .description-label {
    float: left;
  }

  .newline {
    display: none;
  }  

  .new-newline {
    display: block;
  }

  #icon {
    float: left;
    width: 65px;
    height: 65 px;
    border-radius: 50%;
    display: block;
    margin-left: -81px;
    margin-right: 16px;
    transition: 0.25s ease-out;
    filter: grayscale(85%);
  }
  
  #icon:hover {
    transition: 0.3s ease-in;
    filter: grayscale(0%);
  }
}

/* ListSkills Component */

.skill-pill {
  color: #f4f4f5;
  padding: 5px;
  padding-top: 2px;
  padding-bottom: 2px;
  margin: 2px;
  display: inline-block;
  border: 2px solid #007bed; 
  border-radius: 10%;
  font-size: .9em;
  opacity: .9;
}

/* Projects Layout */

.projects-container {
  display: flex;
  flex-wrap: wrap;
  padding: 0 4px;
}

.column {
  flex: 100%;
  max-width: 100%;
  padding: 0 4px;
}

.item-wrapper {
  margin-top: 8px;
  vertical-align: middle;
  position: relative;
  width: 100%;
}

.item-wrapper img {
  opacity: 1;
  display: block;
  width: 100%;
  height: auto;
  transition: .5s ease;
  /* backface-visibility: hidden; */
}

.item-wrapper:hover img {
  opacity: .2;
}

.middle {
  transition: .5s ease;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  width: 90%;
  text-align: center;
  z-index: 50;
}

.text {
  color: #f4f4f5;
  font-size: 16px;
  padding: 16px 32px;
  z-index: 50;
}

.item-wrapper:hover .middle {
  opacity: 1;
}

.project-title {
  font-size: x-large;
  font-weight: bold;
}

.project-desc {
  font-size: small;
}

.project-github {
  color: #f4f4f5;
  border: 2px solid #007bed;
  border-radius: 10%;
  background-color: #212122;
  padding: 3px;
  text-decoration: none;
  transition: .3s;
  opacity: .65;
}

.project-github:hover {
  transition: .3s;
  opacity: 1;
}

@media screen and (min-width: 750px) {
  .column {
    flex: 50%;
    max-width: 50%;
    padding: 0 4px;
  }
} 

/* FOOTER */
.footer {
  margin: 2em 0 1em;
  text-align: center;
}

.footer a {
  color: #f4f4f5;
  text-decoration: none;
  opacity: .5;
  transition: 0.2s;
}

.footer a:hover {
  opacity: .85;
  transition: 0.3s;
}

/* Skills Section */

.box {
  display: inline-block;
  width: 100px;
  box-sizing: border-box;
  margin-bottom: 30px;
  text-align: center;
}

.skill-logo {
  height: 80px;
  margin: 10px 0px;
  opacity: .85;
  transition: 0.2s;
}

.skill-logo:hover {
  transition: 0.2s;
  transform: scale(1.1);
  opacity: 1;
}

.skill-caption {
  text-align:center;
  color: #f4f4f5;
  padding: 0;
  margin: 0;
}

@media screen and (min-width: 500px) {
  .box {
    width: 200px;
  }

  .skill-logo {
    height: 90px;
    margin: 20px 0px;
  }
} 

@media screen and (min-width: 750px) {
  .box {
    width: 220px;
  }

  .skill-logo {
    height: 100px;
    margin: 20px 0px;
  }
} 

